body {
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  width: 80%;
  max-width: 1200px;
}

.task {
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 15px;
  box-shadow: 0 2px 4px #0000001a;
}

.task p {
  margin: 5px 0;
}

.task input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 3px;
  width: calc(100% - 10px);
  margin-bottom: 10px;
  padding: 10px;
}

.task button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.task button:hover {
  background-color: #0056b3;
}

.task button.remove {
  background-color: #dc3545;
}

.task button.remove:hover {
  background-color: #bd2130;
}

.task button.done {
  background-color: #28a745;
}

.task button.done:hover {
  background-color: #218838;
}
